

















	export default{
		data(){
			return{
				agentInfo: null
			}
		},
    onLoad() {
    	this.$http({
        url: this.$apis.articles,
        method: 'GET',
        data: {
          cat_id: 2
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.agentInfo = data.data[0]
            this.agentInfo.content = this.agentInfo.content.replace(/\<img/gi, '<img style="display:block;max-width:100%;height:auto" ')
          } else {
            uni.showToast({
            	title: '请求数据失败',
              icon: 'none'
            })
          }
          this.reqing = false
        }
      })
    },
		methods:{
      gotoIndex() {
        uni.switchTab({
        	url: '/pages/shop/index'
        })
      },
      gotoAgent() {
        this.$navigateTo({
        	url: '/pages/mine/agent'
        })
      }
		}
	}
